:root {
  --accent-color: #ff6b08;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

header {
  height: 230px;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("header-main.b2afa59c.jpeg");
}

.section {
  padding: 60px 0;
}

.container {
  width: 882px;
  margin: 0 auto;
}

.list {
  list-style: none;
}

.header {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 37px;
  display: flex;
}

.navigation {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.navigation__logo {
  color: #fff;
  text-align: center;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__menu {
  gap: 29px;
  display: flex;
}

.navigation__item {
  cursor: pointer;
  position: relative;
}

.navigation__link {
  color: #fff;
  text-transform: uppercase;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__link--current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--accent-color);
  display: flex;
  position: absolute;
}

.header__buttons {
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.header__button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .3s, color .3s;
}

.header__button:hover {
  background-color: var(--accent-color);
  color: #fff;
}

.header__button:focus {
  background-color: var(--accent-color);
  color: #fff;
  outline: none;
}

.form {
  justify-content: center;
  display: flex;
  position: relative;
}

.form__input {
  width: 336px;
  height: 22px;
  color: #fff;
  background: none;
  border: none;
  border-bottom: .5px solid #fff;
}

.form__input:focus {
  border: none;
  border-bottom: .5px solid var(--accent-color);
  width: 336px;
  height: 22px;
  color: #fff;
  background: none;
}

.form__button {
  color: #fff;
  background: none;
  border: none;
  position: absolute;
  top: 2px;
  right: 278px;
}

.movies-gallery {
  flex-wrap: wrap;
  gap: 30px;
  display: flex;
}

.movie-card {
  width: 274px;
}

.movie-card:hover {
  cursor: pointer;
}

.movie-card__poster {
  width: 274px;
  height: 398px;
  border-radius: 5px;
  overflow: hidden;
}

.movie-card__image {
  width: 274px;
  height: 398px;
  object-fit: cover;
}

.movie-card__text {
  color: #000;
  padding-top: 10px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.movie-card__text--accent {
  color: var(--accent-color);
}

.pagination__list {
  justify-content: center;
  display: flex;
}

.pagination__item {
  width: 40px;
  height: 40px;
  color: #000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.pagination__item :not(.pagination__item--dots) {
  cursor: pointer;
}

.pagination__item--arrow {
  background: #f7f7f7;
}

.pagination__item--active {
  background: var(--accent-color);
  color: #fff;
}

footer {
  background: #f7f7f7;
}

.footer {
  height: 77px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__text {
  color: #545454;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.footer__text--accent {
  color: var(--accent-color);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .movies-gallery {
    width: 768px;
    justify-content: center;
  }

  .container {
    width: 768px;
  }

  .form__button {
    right: 220px;
  }
}

@media screen and (max-width: 767px) {
  .movies-gallery {
    width: 320px;
    justify-content: center;
  }

  .container {
    width: 320px;
  }

  .footer__text {
    font-size: 20px;
    line-height: 35px;
  }

  .form__button {
    right: 30px;
  }

  .form__input {
    width: 270px;
  }
}

.members__info-modal {
  width: 90vw;
  height: 80vh;
  background-color: #fff;
  border-radius: 3px;
  margin: 0 auto;
  padding: 50px 20px 30px;
  display: flex;
  position: relative;
}

.members__info-container {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 10px;
  display: flex;
  overflow-y: scroll;
}

.members__info-member {
  flex-direction: column;
  padding: 10px;
  display: flex;
}

.members__info-member--name, .members__info-member--role {
  text-align: center;
  padding: 10px;
}

.members__info-member--picture {
  width: 300px;
  height: 400px;
  background-position-x: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 3px;
  margin-bottom: 20px;
}

.member1 {
  background-image: url("Jairo_Caracas.a37e883b.jpeg");
}

.member2 {
  background-image: url("Andres_Urango.5e8d3fbc.jpeg");
}

.member3 {
  background-image: url("David_Sora.fcc19d36.jpeg");
}

.member4 {
  background-image: url("Steven_Romero.2c504bfd.jpeg");
}

.members__info-member--links {
  width: 100%;
  justify-content: space-around;
  padding: 10px 0;
  display: flex;
}

.members__info-member--linkedin, .members__info-member--github {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: box-shadow .4s;
  display: flex;
}

.members__info-member--linkedin:hover, .members__info-member--github:hover {
  background-color: var(--accent-color);
  fill: #fff;
  box-shadow: 4px 4px 15px #888;
}

